import React from 'react'
import s from './Settings.module.css'
import cn from 'classnames'

const Settings = (props) => {
    return (
        <div>
            <div className={s.flexBox}>
                <div>Change Email | Password </div><a className={s.href} target='blank' href='https://social-network.samuraijs.com/account'>change</a>
            </div>
            <div className={s.flexBox}>
                <div>Open my profile control</div><a className={s.href} target='blank' href='https://social-network.samuraijs.com/account'>open</a>
            </div>
            <div className={s.flexBox}>
                <div>Count of my requests on the Server</div><a className={s.href} target='blank' href='https://social-network.samuraijs.com/Default/Default/Limits'>view</a>
            </div>
            <div className={cn(s.flexBox, s.lastFlexBox)}>
                <div>View | Upgrade my API</div><a className={s.href} target='blank' href='https://social-network.samuraijs.com/Default/Payment/MakeSubscription'>upgrade</a>
            </div>
        </div>
    )
}

export default Settings